// Workshop List Styling (Targeting the <ul> and <li> in your index.html)
.workshop-list {
    // Basic list cleanup
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: $padding-large; // Spacing between workshop items
}

.workshop-item {
    background-color: white;
    border: 1px solid $color-border;
    border-radius: 8px;
    padding: $padding-large;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s; // Animation for hover

    &:hover {
        transform: translateY(-5px); // Lift the card slightly on hover
        box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
    }

    h2 {
        margin-top: 0;
        margin-bottom: $padding-small;
    }

    h3 {
        // Edition/Date style
        color: $color-secondary;
        font-size: 0.9rem;
        font-weight: normal;
        margin-bottom: $padding-small;
    }

    p {
        font-size: 0.95rem;
    }
}

// --- Styling for Single Workshop Page ---
.workshop-notes-section {
    margin-top: $padding-large * 2;
    padding-top: $padding-large;
    border-top: 1px solid $color-border;

    h2 {
        color: $color-text; // Make this less dominant than the main workshop title
        margin-bottom: $padding-large;
    }
}

.workshop-notes-list {
    list-style: none;
    padding: 0;
}

.note-link-item {
    margin-bottom: $padding-small;
}

.note-link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    padding: $padding-large;
    border: 1px solid $color-border;
    border-radius: 4px;
    font-size: 1.1rem;
    color: $color-text;
    transition: all 0.2s ease-in-out;
    
    // Icon for visual cue
    .note-icon {
        color: $color-primary;
        font-weight: bold;
        transition: transform 0.2s;
    }

    &:hover {
        background-color: $color-background;
        border-color: $color-primary;
        transform: translateX(5px); // Subtle slide to show interactivity
        
        .note-icon {
            transform: translateX(3px);
        }
    }
}
